<template>
  <section class="jumbotron">
    <h2 class="sub-header">肖战代言查询</h2>
    <div class="container">
      <input type="text" placeholder="请输入检索内容" v-model="keyword" class="form-control" @keyup.enter="searching">
      <button @click="searching" class="btn-lg btn-primary">搜索</button>
    </div>
  </section>
</template>

<script>
export default {
  name: 'kwSearch',
  data () {
    return {

    }
  },
  computed: {
    keyword: {
      set (value) {
        this.$store.commit('updateKeyword', value)
        this.$router.push({
          query: {
            ...this.$route.query,
            keyword: value
          }
        })
        // this.searching()
      },
      get () {
        return this.$route.query.keyword || this.$store.state.keyword || ''
      },
    }
  },
  methods: {
    searching () {
      this.$store.commit('updateList', this.keyword)
    }
  },
  watch:{
    '$store.state.companyData':{
      handler () { 
        this.searching()
      },
    }
  },
}
</script>

<style scoped>
.container{
  display: flex;
  flex-direction: row;
  width: 100vw;
  height: auto;
}
input{
  width: 60vw;
  height: auto;
}
h2{
  text-align: center;
}
</style>