<template>
  <div class="card">
    <div class="table-responsive">
      <!-- <template v-for="item in $store.state.showInfo">
        <input :key="item.id" :id="item.id" type="checkbox" v-model="item.switch">
        <label :key="`id-${item.id}`" :for="item.id">{{item.name}}</label>
      </template> -->
      <input key="ID" id="ID" type="checkbox" v-model="showCol.ID">
      <label for="ID">{{$store.state.showInfo[0].name}}</label>
      <input key="Title" id="Title" type="checkbox" v-model="showCol.Title">
      <label for="Title">{{$store.state.showInfo[1].name}}</label>
      <input key="Product" id="Product" type="checkbox" v-model="showCol.Product">
      <label for="Product">{{$store.state.showInfo[2].name}}</label>
      <input key="Stock" id="Stock" type="checkbox" v-model="showCol.Stock">
      <label for="Stock">{{$store.state.showInfo[3].name}}</label>
    </div>

    <div class="table-responsive">
      <table class="table table-striped">
        <thead>
          <tr>
            <template>
              <th v-for="item in $store.state.showInfo" :key="item.id" v-show="item.switch">{{item.name}}</th>
            </template>
          </tr>
        </thead>
        <tbody>
          <tr v-for="com in $store.state.filteredData" :key="com.id">
            <td v-show="$store.state.showInfo[0].switch">{{com.id}}</td>
            <td v-show="$store.state.showInfo[1].switch">{{com.title}}</td>
            <td v-show="$store.state.showInfo[2].switch">{{com.product}}</td>
            <td v-show="$store.state.showInfo[3].switch">{{ com | outputStock }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'comList',
  data () {
    return {
      showCol: {
        ID: this.$route.query.ID == 'true' || this.$store.state.showInfo[0].switch,
        Title: this.$route.query.Title == 'true' || this.$store.state.showInfo[1].switch,
        Product: this.$route.query.Product == 'true' || this.$store.state.showInfo[2].switch,
        Stock: this.$route.query.Stock == 'true' || this.$store.state.showInfo[3].switch
      },
    }
  },
  filters: {
    outputStock (value) {
      let str = ''
      if (value.stock_hk) str += '港股：' + value.stock_hk + ' '
      if (value.stock_A) str += 'A股：' + value.stock_A
      if (value.stock_nasdaq) str += '美股：' + value.stock_nasdaq
      return str
    }
  },
  watch: {
    'showCol.ID' (value) {
      this.$store.state.showInfo[0].switch = value
      this.$router.push({
        query: {
          ...this.$route.query,
          ID: value
        }
      })
    },
    'showCol.Title' (value) {
      this.$store.state.showInfo[1].switch = value
      this.$router.push({
        query: {
          ...this.$route.query,
          Title: value
        }
      })
    },
    'showCol.Product' (value) {
      this.$store.state.showInfo[2].switch = value
      this.$router.push({
        query: {
          ...this.$route.query,
          Product: value
        }
      })
    },
    'showCol.Stock' (value) {
      this.$store.state.showInfo[3].switch = value
      this.$router.push({
        query: {
          ...this.$route.query,
          Stock: value
        }
      })
    }
  },


  
}
</script>

<style scoped>
table{
  width: 100vw;
  text-align: left;
}
</style>