import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)

const state = {
  keyword: '',
  showInfo: [
    {
      name: "社会信用代码",
      switch: false,
      id: 'ID'
    },
    {
      name: "公司名称",
      switch: true,
      id: 'Name'
    },
    {
      name: "产品名",
      switch: true,
      id: 'Product'
    },
    {
      name: "股票",
      switch: false,
      id: 'Stock'
    },
  ],
  companyData: {},
  filteredData: {}
}

const actions = {

}

const mutations = {
  updateList(state, keyword) {
    // console.log("aaa", state.companyData)
    state.filteredData = state.companyData.company.filter(x => (x.title.includes(keyword)) || (x.category.includes(keyword)) || (x.product.includes(keyword)))
  },
  updateKeyword(state, keyword) {
    state.keyword = keyword
  },
  updateShowInfo(state, showInfo) {
    state.showInfo[0].switch = showInfo.ID
    state.showInfo[1].switch = showInfo.Name
    state.showInfo[2].switch = showInfo.Product
    state.showInfo[3].switch = showInfo.Stock
  },
}

export default new vuex.Store({
  actions,
  mutations,
  state
})